.e-icon {
  display: inline-block;
  text-align: center;
  width: 16px;
  font-size: 16px;
  line-height: 16px;
}
.e-icon-fontsize-large {
  width: 18px;
  font-size: 18px;
  line-height: 18px;
}
.e-icon-fontsize-medium {
  width: 15px;
  font-size: 15px;
  line-height: 15px;
}
.e-icon-fontsize-small {
  width: 13px;
  font-size: 13px;
  line-height: 13px;
}


.c-icon-spinner {
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}


.c-icon {
  position: relative;
  display: inline-flex;
  font-size: 20px;
  font-weight: inherit;
  align-items: center;
  justify-content: center;
}

.c-icon-fontsize-inherit {
  font-size: inherit;
}

.c-icon,
.c-icon-img {
  &.c-icon-fontsize-large {
    font-size: 18px;
  }
  &.c-icon-fontsize-medium {
    font-size: 15px;
  }
  &.c-icon-fontsize-small {
    font-size: 13px;
  }
}

.c-icon-img {
  background: currentColor;
  display: block;
  flex-shrink: 1;
  font-size: inherit;
  height: 1em;
  width: 1em;
  mask: center/contain;
  box-sizing: inherit;
  color-scheme: dark;
}

.c-icon:before {
  display: inline-block;
  font-family: icon;
  font-size: inherit;
  font-style: normal;
  font-weight: 400;
  line-height: 1;
  color: inherit;
}
.c-icon--inline {
  width: 20px;
  height: auto;
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
}
.c-icon--inline:before {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -0.5em;
  margin-left: -0.5em;
  width: 1em;
  height: 1em;
}
.c-icon--inline:after {
  content: "\200B";
}
.c-icon--inherit {
  width: 1em;
}
.c-icon--inherit:before {
  font-size: inherit;
}
.c-icon--spin:before {
  animation: c-icon--spin 1s linear 0s infinite normal none;
}
@keyframes c-icon--spin {
  0% {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(1turn);
  }
}
.c-deprecated-icon:before {
  font-family: Slack v2;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  display: inline-block;
  vertical-align: middle;
}
.c-deprecated-icon--vertical-align-top:before {
  vertical-align: top;
}
.c-deprecated-icon--vertical-align-bottom:before {
  vertical-align: bottom;
}
.c-deprecated-icon--vertical-align-baseline:before {
  vertical-align: initial;
}
.c-deprecated-icon--inherit:before {
  font-size: inherit;
}
